body {
  min-height: 100vh;
  background: linear-gradient(135deg, #b7f09f, #56bbb9); }

.content {
  margin: auto;
  max-width: 640px;
  width: 100%; }
  .content-inner {
    overflow: hidden;
    margin: 0 6px;
    background: rgba(255, 255, 255, 0.9); }

.tabs {
  position: relative;
  min-height: 100vh; }
  .tabs:after {
    content: '';
    display: table;
    clear: both; }
  .tabs input[type='radio'] {
    display: none; }

.tab-content {
  width: 100%;
  height: calc(100vh - 77px);
  float: left;
  overflow-y: auto; }

.tab-item {
  overflow: hidden;
  float: left;
  width: 50%;
  padding: 24px 0 0;
  text-align: center;
  font-size: 1em;
  color: #3f6669;
  cursor: pointer; }
  .tab-item:after {
    content: '';
    display: block;
    margin: 6px 0;
    height: 3px;
    background: #3f6669;
    border-radius: 3px;
    transition: transform 1s; }
  .tab-item .value {
    font-size: .9rem; }

#tab1 + .tab-item:after {
  transform: translateX(100%); }

#tab1:checked + .tab-item:after {
  transform: translateX(0%); }

#tab2 + .tab-item:after {
  transform: translateX(-100%); }

#tab2:checked + .tab-item:after {
  transform: translateX(0%); }

#tab-content1 {
  position: absolute;
  top: 77px;
  transform: translateX(-100%);
  transition: transform 1s; }

#tab1:checked ~ #tab-content1 {
  transform: translateX(0%); }

#tab-content2 {
  position: absolute;
  top: 77px;
  transform: translateX(100%);
  transition: transform 1s; }

#tab2:checked ~ #tab-content2 {
  transform: translateX(0%); }

.item-list:after {
  content: '';
  display: table;
  clear: both; }

.item {
  float: left;
  width: calc(100% / 3);
  padding: 12px;
  text-align: center;
  color: #3f6669; }
  .item .meta {
    padding-bottom: 3px;
    font-size: .9rem;
    font-weight: 700;
    color: #69a1a5; }
  .item .value {
    display: inline-block;
    padding-left: 3px;
    font-size: 1.3rem;
    color: #3f6669; }
  .item .image {
    display: inline-block;
    padding-bottom: 3px; }
  .item .name {
    font-weight: 300;
    padding-bottom: 3px; }
  .item .status {
    width: 100%;
    height: 3px;
    border-radius: 3px;
    background: #72ecb8; }

